// pages/provider/dashboard.js
import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import toast from "react-hot-toast";

export default function ProviderDashboard() {
  const router = useRouter();
  const [provider, setProvider] = useState(null);

  useEffect(() => {
    const saved = localStorage.getItem("provider");
    if (!saved) {
      toast.error("Please log in first");
      router.push("/provider/login");
      return;
    }
    setProvider(JSON.parse(saved));
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("provider");
    toast.success("Logged out");
    router.push("/provider/login");
  };

  if (!provider) return null;

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 to-orange-100">
      <header className="bg-white shadow-md p-4 flex justify-between items-center">
        <h1 className="text-xl font-bold text-orange-600">Khedme Provider Dashboard</h1>
        <button
          onClick={handleLogout}
          className="bg-orange-500 text-white px-4 py-2 rounded-lg hover:bg-orange-600 transition"
        >
          Logout
        </button>
      </header>

      <main className="max-w-3xl mx-auto mt-8 p-6 bg-white rounded-2xl shadow-lg">
        <h2 className="text-2xl font-semibold text-gray-800 mb-4">
          Welcome, Provider #{provider.id}
        </h2>

        <p className="text-gray-600 mb-6">
          Phone: <span className="font-medium">{provider.phone}</span>
        </p>

        <div className="border-t pt-4 space-y-3">
          <h3 className="text-lg font-semibold text-orange-500">Next Steps</h3>
          <ul className="list-disc list-inside text-gray-700">
            <li>Set up your profile (photo, skills, district)</li>
            <li>Browse available jobs</li>
            <li>Toggle your availability</li>
          </ul>
        </div>
      </main>
    </div>
  );
}
